import React, { useState, useEffect } from 'react';
import { useInView } from 'react-intersection-observer';
import cn from 'classnames';

import s from './BurgerIngredients.module.css';
import IngredientsTab from '../IngredientsTab/IngredientsTab';
import BurgerIngredientType from '../BurgerIngredientType/BurgerIngredientType';
import BurgerIngredient from '../BurgerIngredient/BurgerIngredient';
import { useCustomSelector } from '../../utils/hooks';
import { TIngredient } from '../../utils/types';

function BurgerIngredients() {
	const ingredients: TIngredient[] = useCustomSelector(
		(state) => state.burgerIngredient.data
	);

	const [ingredientInView, setIngredientInView] = useState('bun');

	const [refBun, inViewBun] = useInView();
	const [refSauce, inViewSauce] = useInView();
	const [refMain, inViewMain] = useInView();

	useEffect(() => {
		if (inViewBun) {
			setIngredientInView('bun');
		} else if (inViewSauce) {
			setIngredientInView('sauce');
		} else if (inViewMain) {
			setIngredientInView('main');
		}
	}, [inViewBun, inViewSauce, inViewMain]);

	return (
		<section className={s.column}>
			<p className='text text_type_main-large mt-10 mb-5'> </p>
			<IngredientsTab
				ingredientInView={ingredientInView}
				setIngredientInView={setIngredientInView}
			/>
			<div className={cn(s.ingredients, 'custom-scroll')}>
				<BurgerIngredientType name='' id='bun' ref={refBun}>
					{ingredients
						.filter((ingredient: TIngredient) => ingredient.type === 'bun')
						.map((ingredient) => {
							return (
								<BurgerIngredient
									ingredient={ingredient}
									key={ingredient._id}
								/>
							);
						})}
				</BurgerIngredientType>
				<BurgerIngredientType name='' id='sauce' ref={refSauce}>
					{ingredients
						.filter((ingredient) => ingredient.type === 'sauce')
						.map((ingredient) => {
							return (
								<BurgerIngredient
									ingredient={ingredient}
									key={ingredient._id}
								/>
							);
						})}
				</BurgerIngredientType>
				<BurgerIngredientType name='' id='main' ref={refMain}>
					{ingredients
						.filter((ingredient) => ingredient.type === 'main')
						.map((ingredient) => {
							return (
								<BurgerIngredient
									ingredient={ingredient}
									key={ingredient._id}
								/>
							);
						})}
				</BurgerIngredientType>
			</div>
		</section>
	);
}

export default BurgerIngredients;
